import React from 'react';
import Image from 'react-bootstrap/Image';
import CoutDown from '../components/Timer'
import Carousel from 'react-bootstrap/Carousel';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../assets/css/Banner.css';
import '../assets/css/Carousel-custom.css';
const Announce_banner = () => {
    return (
        <>            
            <section className="wedding_banner" id='home'>
                <div className="wedding_announcement mt-5" style={{ top: 0 }}>
                    <h2 className="loader wow fadeInLeft mt-5" data-wow-delay="0.2s" data-wow-duration="1.2s" style={{ marginTop: '100px', marginBottom: 0, cssText: 'margin-top: 25vh !important;' }}>
                        <span>N</span>
                        <span>i</span>
                        <span>c</span>
                        <span>o</span>
                        <span>l</span>
                        <span>e</span>
                    </h2>
                </div>
            </section>
            <iframe
                src="https://missael306.github.io/galeria/"
                width="100%"
                height="600"
                style={{ border: "none", zIndex:-99 }}
            ></iframe>            
        </>
    )
}
export default Announce_banner;